import { Component, NgZone, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { IonSlides } from '@ionic/angular';

@Component({
  selector: 'app-welcome-screens',
  templateUrl: './welcome-screens.page.html',
  styleUrls: ['./welcome-screens.page.scss'],
})

export class WelcomeScreensPage implements OnInit{

  @ViewChild('slideWithNav', { static: false }) slideWithNav: IonSlides; 
  allData:any=[];
  netButtonText:string="Next";
  preButtonText:string="Previous";
  showPreviewBtn:boolean=false;
  slideOpts = {
    initialSlide: 0,
    speed: 400,
    zoom:false,
    slidesPerView:1,
    spaceBetween: 10,
    centeredSlides: true
  };
  showNextButton:boolean=false;

  constructor(private router:Router,private zone:NgZone) { }









  ngOnInit() {
    this.allData=[
      {
        "image":"../../assets/welcomeScreen/WelcomOne.png",
        "skipText":"Skip",
        "title":"",
        "subTitle":"Welcome to your workspace",
      },
      {
        "image":"../../assets/welcomeScreen/WelcomThird.png",
        "skipText":"Skip",
        "title":"Say Cheers",
        "subTitle":"Say Cheers to your peers for a job done well.",
      },
      {
        "image":"../../assets/welcomeScreen/WelcomTwo.png",
        "skipText":"Skip",
        "title":"Newsfeed",
        "subTitle":"Find the latest company updates and connect with your colleagues.",
      },
    ]
  }

  slideDidChange(){
    this.zone.run(()=>{
      console.log('swipeChange==');
      this.showPreviewBtn=true;
      this.slideWithNav.isEnd().then((val) => {
        if(val==true){
          this.showNextButton=val;
          console.log("next button==", this.showNextButton);
          // this.router.navigate(['/organisation-name']);
          // this.netButtonText="Start";
          this.netButtonText="Next";
        }else{
          // this.netButtonText="Next";
          this.showNextButton=val;
          this.netButtonText="";
        }
      });
      this.slideWithNav.getActiveIndex().then((num)=>{
        console.log('swipeChange num==', num);
        if(num<1){
          this.showPreviewBtn=false;
        }else{
          this.showPreviewBtn=true;
        }
      });
    });
  }

  seeNextSlide(){
    this.zone.run(()=>{
      this.slideWithNav.isEnd().then((val) => {
        if(val==true){
          this.router.navigate(['/login']);
          // this.router.navigate(['/organisation-name']);
        }else{
          this.slideWithNav.slideNext(500, true);
        }
      });
    });
  }

  

  seePreSlide(){    
    this.zone.run(()=>{
      this.slideWithNav.getActiveIndex().then((num)=>{
        console.log('preview Slider==', num);
        if(num>=1){
        this.slideWithNav.lockSwipes(false).then(()=>{
          this.slideWithNav.slideTo(num-1, 500, true).then(()=>{
            console.log('goBack slide sucessfully');
            this.netButtonText="Next";
          })
        });
        }else{
          this.showPreviewBtn=false;
        }
      });
    });
  }
  
  seeLoginPage(){
    this.zone.run(()=>{
      // this.router.navigate(['/organisation-name']);
      // this.router.navigate(['/award-list']);
      this.router.navigate(['/login']);
      
    });
  }









}